
#perLineText {
    text-align: right;
}

#alarmText span {
    display: block;
    background-color: #dd4b39;
    font-weight: 600;
    color: #ffffff;
}

.form-control.riskNumber {
    width: 120px;
    font-size:13px;
    height: 30px;
    padding: 6px;
    margin: 0px 4px;
    display: inline;
}

#optionRow {
    color: red;
    padding: 2px;
}

#btnExceptionFileDefault, #btnIncludeFileDefault{
    margin-top: 3px;
    height: 26px;
    padding: 2px 6px;
}

#exceptionProjectTree {
    height: 250px;
    overflow: scroll;
}

section.content {
	.tab-pane {
		hr {
		    border-top: 1px solid #e9e9e9;;
		}
		.box-header {
			font-size: 15px;
			font-weight: 600;
			margin-bottom: 10px;
			i.fa {
				color: #252525;
			}
		}
		.form-group 
		{
			font-size: 13px;
			font-weight: 600;
			.controll-label
			{
			}
			.risk-line
			{
				color: #666666;
			}
			.btn-group
			{
				.perLine
				{
					font-size: 13px;
					font-weight: 600;
				}
			}
		}
	}
	
	
	.checkbox-inline
	{
		margin-left:0;
		margin-right:10px;
		label {
			padding-left:0;
		}
	}
	
	.color-gray {
		color: #666666
	}
}

.tooltip-right-top {
	.tooltip {
		.tooltip-inner {
           margin-top: 30px;
		}
	}
}